import actions from '@/vuex/actions';

import Vue from 'vue';

import Vuex from 'vuex';
Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    config: {},
    authenticated: false,
    fragmentContent: '',
    editedPage: {},
  },
  getters: {},
  mutations: {
    AUTH_USER(state) {
      state.authenticated = true;
    },

    CHANGE_EDITED_PAGE(state, page) {
      console.log('CHANGE_EDITED_PAGE', page);
      state.editedPage = page;
      state.editedPageDest = page.destination;
    },

    SET_CONFIG(state, config) {
      state.config = config;
    },
  },
  actions
});

window.$s = store;

export default store;



