//Uses the https://github.com/github-tools/github library under the hood and exposes it as `gh` property
var ctorptr;

var ctor = {

    /**
     * Sets the current repository to make push to
     * @public
     * @param {string} userName Name of the user who owns the repository
     * @param {string} repoName Name of the repository
     * @return void
     */
    init(githubObject, repository) {
        this.gh = githubObject;
        this.repo = repository;

        this.filesToCommit = [];
        this.currentBranch = {};
        this.newCommit = {};
    },

    /**
     * Sets the current branch to make push to. If the branch doesn't exist yet,
     * it will be created first
     * @public
     * @param {string} branchName The name of the branch
     * @return {Promise}
     */
    setBranch(branchName) {
        if (!this.repo) {
            throw 'Repository is not initialized';
        }

        return this.repo.listBranches().then((branches) => {

            let branchExists = branches.data.find( branch => branch.name === branchName );

            if (!branchExists) {
                return this.repo.createBranch('master', branchName)
                    .then(() => {
                        this.currentBranch.name = branchName;
                    });
            } else {
                this.currentBranch.name = branchName;
            }
        });
    },

    /**
     * Makes the push to the currently set branch
     * @public
     * @param  {string}   message Message of the commit
     * @param  {object[]} files   Array of objects (with keys 'content' and 'path'),
     *                            containing data to push
     * @return {Promise}
     */
    pushFiles(message, files) {
        if (!this.repo) {
            throw 'Repository is not initialized';
        }
        if (!this.currentBranch.hasOwnProperty('name')) {
            throw 'Branch is not set';
        }

        return this.getCurrentCommitSHA()
            .then(this.getCurrentTreeSHA)
            .then( () => this.createFiles(files) )
            .then(this.createTree)
            .then( () => this.createCommit(message) )
            .then(this.updateHead)
            .catch((e) => {
                console.error(e);
            });
    },

    /**
     * Sets the current commit's SHA
     * @private
     * @return {Promise}
     */
    getCurrentCommitSHA() {
      var repo = this.repo;
        return repo.getRef('heads/' + this.currentBranch.name)
            .then((ref) => {
                this.currentBranch.commitSHA = ref.data.object.sha;
            });
    },

    /**
     * Sets the current commit tree's SHA
     * @private
     * @return {Promise}
     */
    getCurrentTreeSHA() {
      console.log('this', ctorptr);

      var repo = ctorptr.repo;
      console.log('rrr', repo);

        return repo.getCommit(ctorptr.currentBranch.commitSHA)
            .then((commit) => {
                ctorptr.currentBranch.treeSHA = commit.data.tree.sha;
            });
    },

    /**
     * Creates blobs for all passed files
     * @private
     * @param  {object[]} filesInfo Array of objects (with keys 'content' and 'path'),
     *                              containing data to push
     * @return {Promise}
     */
    createFiles(filesInfo) {
        let promises = [];
        let length = filesInfo.length;

        for (let i = 0; i < length; i++) {
            promises.push(this.createFile(filesInfo[i]));
        }

        return Promise.all(promises);
    },

    /**
     * Creates a blob for a single file
     * @private
     * @param  {object} fileInfo Array of objects (with keys 'content' and 'path'),
     *                           containing data to push
     * @return {Promise}
     */
    createFile(fileInfo) {
        return this.repo.createBlob(fileInfo.content)
            .then((blob) => {
                this.filesToCommit.push({
                    sha: blob.data.sha,
                    path: fileInfo.path,
                    mode: '100644',
                    type: 'blob'
                });
            });
    },

    /**
     * Creates a new tree
     * @private
     * @return {Promise}
     */
    createTree() {
        return ctorptr.repo.createTree(ctorptr.filesToCommit, ctorptr.currentBranch.treeSHA)
            .then((tree) => {
                ctorptr.newCommit.treeSHA = tree.data.sha;
            });
    },

    /**
     * Creates a new commit
     * @private
     * @param  {string} message A message for the commit
     * @return {Promise}
     */
    createCommit(message) {
        return this.repo.commit(this.currentBranch.commitSHA, this.newCommit.treeSHA, message)
            .then((commit) => {
                this.newCommit.sha = commit.data.sha;
            });
    },

    /**
     * Updates the pointer of the current branch to point the newly created commit
     * @private
     * @return {Promise}
     */
    updateHead() {
        return ctorptr.repo.updateHead('heads/' + ctorptr.currentBranch.name, ctorptr.newCommit.sha);
    },
};

ctorptr = ctor;

export default ctor;



