<template>
  <div>
    <div class="modal is-active" v-if="compilationActive">
      <div class="modal-background"></div>
      <div class="modal-content">
        <h2>
          Saving changes, please wait...
        </h2>
        <h3>
          {{compilationText}}
        </h3>
        <div class="lds-hourglass"></div>
      </div>
    </div>
    <LoginModal :visible="loginModalVisible"/>
    <nav class="navbar" role="navigation" aria-label="main navigation" style="color:white; background-color: #7957D5">
      <div class="navbar-brand">
        Admin
      </div>
      <div class="navbar-end">
        <button class="button is-medium is-primary saveButton" @click="saveAllPages">
          Publish Everything
        </button>
      </div>
    </nav>
    <table style="width: 100%;">
      <tr>
        <td style="width: 230px;">
          <Menu/>
        </td>
        <td>
            <div v-if="editedPage.fragment">
              <nav class="navbar is-warning">
                <h1 class="title">Editing page : {{editedPage.title}}</h1>
                <div class="navbar-end">
                  <button class="button is-medium is-primary saveButton" @click="savePage">
                    Publish Page
                  </button>
                </div>
              </nav>
              <b-tabs v-model="activeTab">
                <b-tab-item label="Content">
                  <Component :is="getEditorComponentName(editedPage.type)" :page="editedPage" :fragment="editedPage"/>
                </b-tab-item>
              </b-tabs>
            </div>
            <div v-else-if="editedPage.fragments">
              <nav class="navbar is-warning">
                <h1 class="title">Editing page : {{editedPage.title}}</h1>
                <div class="navbar-end">
                  <button class="button is-medium is-primary saveButton" @click="savePage">
                    Publish
                  </button>
                </div>
              </nav>
              <b-tabs>
                  <b-tab-item v-for="f in editedPage.fragments" :label="f.title">
                    <Component :is="getEditorComponentName(f.type)" :page="editedPage" :fragment="f"/>
                  </b-tab-item>
              </b-tabs>
            </div>
            <div v-else>
              Click on a page to edit it.
            </div>
          </b-tabs>
        </td>
      </tr>
    </table>
  </div>
</template>
<script>
import EditorHtml from '@/components/EditorHtml';
import EditorGallery from '@/components/EditorGallery';
import Menu from '@/components/Menu';
import LoginModal from '@/components/LoginModal';
import { mapState } from 'vuex';
import { writeGitFile, fragmentContent, getGithubFile } from '@/utils';
import hbs from 'handlebars';

function uploadFragments(component, s, fragments, i, cb) {
  var progressNumber = i + 1;
  component.compilationText = "Writing configuration (" + progressNumber + "/" + (fragments.length + 1) + ")";
  if(i === undefined) i = 0;
  if(i >= fragments.length) {
    cb();
    return;
  }

  var fragmentPath = fragments[i].fragment;
  writeGitFile(s, fragmentPath , fragmentContent(s, fragmentPath)).then(() => {
    uploadFragments(component, s, fragments, i+1, cb);
  });
}

function compileCurrentPage(store, page, config, pageContent, cb) {
  getGithubFile(store, page.template, (responseText) => {
    var source = atob(responseText.content);
    var template = hbs.compile(source);

    if(page.fragment) {
      var data = {
        content: pageContent,
        options: store.state.config.options,
        pages: store.state.config.pages
      };
    } else if(page.fragments) {
      var data = {
        options: store.state.config.options,
        pages: store.state.config.pages
      };

      for (var i = 0; i < page.fragments.length; i++) {
        var content = fragmentContent(store, page.fragments[i].fragment);
        if(page.fragments[i].fragment.endsWith('.json')) {
          try{
            content = JSON.parse(content);
          } catch(e) {

          }
        }

        data[page.fragments[i].id] = content;
      }
    }

    var result = template(data);

    writeGitFile(store, page.destination, result, 'compile ' + page.destination).then(cb());
  });
}

function compilePage(store, page, cb) {
  var fragmentsContent = {};
  return new Promise((resolve) => {

    getGithubFile(store, page.template, (responseText) => {
      var source = atob(responseText.content);
      var template = hbs.compile(source);
      var contentPromises = [];

      if(page.fragment) {
        var data = {
          options: store.state.config.options,
          pages: store.state.config.pages
        };

        contentPromises.push( new Promise((resolve) => {
          getGithubFile(store, page.fragment, (responseText) => {
            data.content = atob(responseText.content);
            resolve();
          });
        }));
      } else if(page.fragments) {
        var data = {
          options: store.state.config.options,
          pages: store.state.config.pages
        };

        function appendFragmentPromise(path, id) {
          contentPromises.push( new Promise((resolve) => {
            getGithubFile(store, path, (responseText) => {
              var content = atob(responseText.content);
              if(path.endsWith('.json')) {
                try{ content = JSON.parse(content); } catch(e) {}
              }

              data[id] = content;
              resolve();
            });
          }));
        }

        for (var i = 0; i < page.fragments.length; i++) {
          appendFragmentPromise(page.fragments[i].fragment, page.fragments[i].id);
        }
      }

      Promise.all(contentPromises).then(() => {
        var result = template(data);
        writeGitFile(store, page.destination, result, 'compile ' + page.destination).then(() => {
          if(cb) cb();
          resolve();
        });
      });
    });
  });
}
export default {
  name: 'Index',
  data() {
    return {
      compilationActive: false,
      activeTab: 0,
      compilationText: '',
      loginModalVisible: true,
    };
  },

  methods: {
    getEditorComponentName(pageType) {
      var editorType = pageType.replace(/-([a-z])/g, function (g) { return g[1].toUpperCase(); });
      editorType = editorType.charAt(0).toUpperCase() + editorType.slice(1);

      return "Editor" + editorType;
    },

    saveAllPages() {
      this.compilationActive = true;
      this.compilationText = "Regenerating all pages. This might take some time...";
      var generatePromise = function(store, page) {
        return () => {
          var p = compilePage(store, page, () => {});
            console.log('p', p)
          return p;
        };
      };

      var promiseArray = [];
      for (var i = 0; i < this.config.pages.length; i++) {
        promiseArray.push(generatePromise(this.$store, this.config.pages[i]));
      }

      var executeSequentially = function(tasks) {
        if (tasks && tasks.length > 0) {
          var task = tasks.shift();

          return task().then(function() {
            return executeSequentially(tasks);
          });
        }

        return Promise.resolve();
      };
      executeSequentially( promiseArray ).then( results => {
        this.compilationActive = false;
      } ).catch( results => {
          // any of promises failed, and you can get all results of them
      } );
    },

    savePage() {
      var s = this.$store;
      this.compilationActive = true;
      if(this.editedPage.fragment) {
        this.compilationText = "Writing configuration (1/1)";
        var _fragmentContent = fragmentContent(this.$store, this.editedPage.fragment);
        writeGitFile(
          s,
          this.editedPage.fragment,
          _fragmentContent
        ).then(() => {
          compileCurrentPage(s, this.editedPage, _fragmentContent, this.config, () => {
            this.compilationActive = false;
          });
        });
      } else if(this.editedPage.fragments) {
        uploadFragments(this, s, this.editedPage.fragments, 0, () => {
          compileCurrentPage(s, this.editedPage, undefined, this.config, () => {
            this.compilationActive = false;
          });
        });
      }
    }
  },

  components: {
    EditorHtml,
    EditorGallery,
    LoginModal,
    Menu,
  },

  watch: {
    authenticated(newValue) {
      this.loginModalVisible = !newValue;
    },
    editedPage() {
      this.activeTab = 0;
    }
  },

  computed: mapState({
    editedPage: state => state.editedPage,
    config: state => state.config,
    authenticated: state => state.authenticated,
  })

};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.navbar-brand {
  color: white;
  font-size: 24pt;
  font-weight: bold;
  padding-left: 12px;
}

.saveButton {
  margin: 4px;
}

nav .title {
  font-size: 18pt;
  padding: 10pt;
}
</style>



