<template>
  <div style="width: 100%">
    <div v-if="fragmentContent">
      <b-field label="Title" >
        <b-input v-model="fragmentContent.title" name="title" @input="saveGalleryObject"></b-input>
      </b-field>
      <b-field label="Subtitle">
        <b-input v-model="fragmentContent.subtitle" name="subtitle" @input="saveGalleryObject"></b-input>
      </b-field>

      <div v-for="(img, imgIndex) in fragmentContent.images">
        <div class="card" style="width: 250px; margin: 10px; float: left;">
          <div class="card-image">
            <EditorGalleryImage :img="img" @onUpload="uploadImage(imgIndex, img)" @onRemove="removeImage(img)"/>
          </div>
          <div class="card-content">
            <div class="media">
              <div class="media-content">
                <p class="title is-4">
                  <b-field label="Title">
                    <b-input v-model="img.title" name="Image title" @input="saveImageObject(imgIndex, img)"></b-input>
                  </b-field>
                </p>
              </div>
            </div>
            <div class="content">
              <b-field label="Description">
                <b-input v-model="img.description" name="Image description" @input="saveImageObject(imgIndex, img)"></b-input>
              </b-field>
            </div>
          </div>
        </div>
      </div>
      <div class="card addImageCard" style="width: 250px; margin: 10px; float: left;" @click="addImage" v-if="!fragment.maxImages || fragmentContent.images.length < fragment.maxImages">
        <div class="card-image">
          <img src="../assets/image-placeholder.png" />
        </div>
        <div class="card-content">
          <div class="media">
            <div class="media-content">
              <p class="title is-4">
                Add Image
              </p>
            </div>
          </div>
        </div>
      </div>
      <div style="clear: both;"></div>
    </div>
    <div class="lds-hourglass" v-else></div>
  </div>
</template>
<script>
import EditorGalleryImage from '@/components/EditorGalleryImage';
import { setFragmentContent, fragmentContent as getFragmentContent, getGithubFile} from '@/utils';

import { mapState } from 'vuex';

import { writeGitFile, fragmentContent,  } from '@/utils';

export default {
  props: ["page", "fragment"],

  data() {
    getGithubFile(this.$store, this.fragment.fragment, (responseText) => {
      responseText = atob(responseText.content);
      console.log(responseText);
      try {
        content = JSON.parse(responseText);
        setFragmentContent(this.$store, this.fragment.fragment, JSON.stringify(content, null, 2));
        this.fragmentContent = content;
      } catch(e) {
        var content = { images: [] };
        this.fragmentContent = content;
        setFragmentContent(this.$store, this.fragment.fragment, JSON.stringify(this.fragmentContent, null, 2));
      }
    });

    return {
      selectionRange: undefined,
      fragmentContent: '',
    };
  },

  methods: {
    saveGalleryObject() {
      console.log("saveGalleryObject");
      setFragmentContent(this.$store, this.fragment.fragment, JSON.stringify(this.fragmentContent, null, 2));
    },
    saveImageObject(imgIndex, img) {
      console.log(event, img);
      this.fragmentContent.images[imgIndex] = img;
      setFragmentContent(this.$store, this.fragment.fragment, JSON.stringify(this.fragmentContent, null, 2));
    },

    uploadImage(imgIndex, img) {
      console.log('size', this.fragment.max_image_width, this.fragment.max_image_height)
      cloudinary.openUploadWidget({
        cloud_name: 'gwenaelp',
        upload_preset: 'lgbq0ji5',
        ...this.fragment
      },
        (error, result) => {
          img.src = result[0].secure_url;
          this.saveImageObject(imgIndex, img);
        }
      );

    },

    removeImage(img) {
      var content = this.fragmentContent;
      content.images = content.images.filter(function( obj ) {
          return obj !== img;
      });

      this.fragmentContent = content;
      setFragmentContent(this.$store, this.fragment.fragment, JSON.stringify(this.fragmentContent, null, 2));
    },

    addImage() {
      this.fragmentContent.images.push({});
      setFragmentContent(this.$store, this.fragment.fragment, JSON.stringify(this.fragmentContent, null, 2));
    },
  },

  watch: {
    fragment(newValue) {
      if(newValue) {
        getGithubFile(this.$store, this.fragment.fragment, (responseText) => {
          responseText = atob(responseText.content);
          try {
            var content = JSON.parse(responseText);
            setFragmentContent(this.$store, this.fragment.fragment, JSON.stringify(content, null, 2));
            this.fragmentContent = content;
          } catch(e) {
            var content = { images: [] };
            setFragmentContent(this.$store, this.fragment.fragment, JSON.stringify(content, null, 2));
            this.fragmentContent = content;
          }
        });
      }
    }
  },

  components: {
    EditorGalleryImage,
  },

  computed: {
    config: {
      get() {return this.$store.state.config; },
      set(value) {},
    },
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.addImageCard {
  cursor: pointer;
}

.addImageCard {
  opacity: 0.7;
}

.addImageCard:hover {
  opacity: 1;
}

.lds-hourglass {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.lds-hourglass:after {
  content: " ";
  display: block;
  border-radius: 50%;
  width: 0;
  height: 0;
  margin: 6px;
  box-sizing: border-box;
  border: 26px solid #fff;
  border-color: black transparent black transparent;
  animation: lds-hourglass 1.2s infinite;
}
@keyframes lds-hourglass {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  50% {
    transform: rotate(900deg);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    transform: rotate(1800deg);
  }
}
</style>



