<template>
  <div>
    <span class="menu">
      <span>
        <a v-on:click="upload()">
          <v-icon name="upload"></v-icon>
        </a>
        <a v-on:click="remove()">
          <v-icon name="x-circle"></v-icon>
        </a>
      </span>
    </span>
    <figure class="image is-4by3" @mouseenter="menuOpened = true" @mouseleave="menuOpened = false">
      <img :src="img.src"  v-if="img.src" style="object-fit: contain;" />
      <img src="../assets/image-placeholder.png" v-else />
    </figure>
  </div>
</template>
<script>
export default {
  props: ["img"],
  data() {
    return {
      menuOpened: false,
    }
  },

  methods: {
    upload() {
      this.$emit('onUpload');
    },
    remove() {
      this.$emit('onRemove');
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
figure {
  min-height: 180px;
}

.menu {
  position: absolute;
  z-index: 5;
  width: 100%;
  top:5px;
  text-align: right;
}

.menu span {
  display: inline-block;
  height: 38px;
  padding: 6px;
  border-radius: 12px;
  background: rgba(0,0,0, .5);
}

.menu a {
  color: white;
}
</style>



