<template>
  <div style="padding: 0">
    <div id="toolbar">
      <button class="button is-medium is-primary additionalToolbarButton" @click="openImageUploader"><v-icon name="image"></v-icon></button>
    </div>
    <div class="lds-hourglass" v-if="loading"></div>

    <quill-editor v-else v-model="fragmentContent"
      ref="myQuillEditor"
      @ready="quillReady($event)"
      @change="onEditorChange($event)">
    </quill-editor>
  </div>
</template>
<script>
import hbs from 'handlebars';

import { Quill } from 'vue2-editor/dist/vue2-editor';
import store from './../vuex/store';
import { mapState } from 'vuex';
import { getGithubFile } from '@/utils';


export default {
  props: ["page", "fragment"],

  data() {
    getGithubFile(this.$store, this.fragment.fragment, (responseText) => {
      responseText = atob(responseText.content);

      this.fragmentContent = responseText;
      this.$store.state['fragment-' + this.fragment.fragment] = responseText;
      this.loading = false;
    });

    return {
      loading: true,
      selectionRange: undefined,
      fragmentContent: '',
    };
  },

  methods: {
    onEditorChange(event) {
      this.$store.state['fragment-' + this.fragment.fragment] = event.html;
    },

    openImageUploader() {
      cloudinary.openUploadWidget({ cloud_name: 'gwenaelp', upload_preset: 'lgbq0ji5'},
      (error, result) => {
        var fragmentContent = this.$store.state['fragment-' + this.fragment.fragment];
        var i = this.selectionRange ? this.selectionRange.index : 0;
        var img = '<img src="' + result[0].secure_url + '" />';
        this.$store.state['fragment-' + this.fragment.fragment] = [fragmentContent.slice(0, i), img, fragmentContent.slice(i)].join('');
      });
    },

    openConfig: function() {
      this.isConfigModalOpened = true;
    },

    quillReady(editor, range) {
      this.selectionRange = range;

      editor.on('selection-change', (range) => {
        this.selectionRange = range;
      });
    },

    savePage() {
      this.$store.repository.writeFile(
       'gh-pages', // e.g. 'gh-pages'
       this.page.fragment, // e.g. 'blog/index.md'
       this.$store.state['fragment-' + this.fragment.fragment], // e.g. 'Hello world, this is my new content'
       'edit ' + this.page.fragment, // e.g. 'Created new index'
       function(err) {}
      );
    },
  },

  watch: {
    fragment(newValue) {
      this.loading = true;
      getGithubFile(this.$store, this.fragment.fragment, (responseText) => {
        if(responseText === undefined)
          responseText = '';
        else
          responseText = atob(responseText.content);

        this.$store.state['fragment-' + this.fragment.fragment] = responseText;
        this.fragmentContent = responseText;
        this.loading = false;
      });
    }
  },

  computed: mapState({
    config: state => state.config,
    editedPageDest: state => state.editedPageDest,
  })
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.additionalToolbarButton {
  float:right;
}
.saveButton {
  float: right;
}

</style>



