<template>
  <div>
    <div class="modal is-active" v-if="isDeleteSpinnerOpened">
      <div class="modal-background"></div>
      <div class="modal-content">
        <h2>
          Saving changes, please wait...
        </h2>
        <div class="lds-hourglass"></div>
      </div>
    </div>
    <aside class="menu" style="background: #CDCDCD">
      <p class="menu-label">
        Pages
      </p>
      <ul class="menu-list">
        <li v-for="page in config.pages">
          <a class="is-small" @click="deletePage(page)" v-if="(page.default !== true)" style="float: right;">
            <v-icon name="trash"></v-icon>
          </a>
          <a @click="loadPage(page)"><v-icon name="file"></v-icon> {{page.title}}</a>
        </li>
      </ul>
      <div v-if="config.pages && config.pages.length">
        <button class="button is-medium" @click="openNewPageModal" style="width: 100%;">
          Add page
        </button>

      </div>
      <p class="menu-label">
        Settings
      </p>
      <ul class="menu-list">
        <li>
          <a @click="openConfig()"><v-icon name="settings"></v-icon> Configuration</a>
        </li>
      </ul>
    </aside>
    <b-modal :active.sync="isConfigModalOpened" has-modal-card :canCancel="true">
      <div class="card" style="padding: 10px; width: 500px">
        <h1 style="font-size: 18pt; font-weight: bold;">Configuration</h1>
        <vue-form-generator :schema="config.optionsSchema" :model="config.options"></vue-form-generator>
        <button class="button is-medium is-primary saveButton" @click="saveConfig">
            Save
        </button>
        <button class="button is-medium" @click="closeConfig">
            Close
        </button>
        <div style="clear: both;"></div>
      </div>
    </b-modal>
    <b-modal :active.sync="isNewPageModalOpened" has-modal-card :canCancel="true">
      <div class="card" style="padding: 10px; width: 500px">
        <h1 style="font-size: 18pt; font-weight: bold;">New Page</h1>
        <b-field label="Page Name" >
          <b-input v-model="newPageName" name="sitename"></b-input>
        </b-field>
        <button class="button is-medium is-primary saveButton" @click="createNewPage">
            Create
        </button>
        <button class="button is-medium" @click="closeNewPageModal">
            Close
        </button>
        <div style="clear: both;"></div>
      </div>
    </b-modal>

  </div>
</template>
<script>
import { mapState } from 'vuex';
import { getGithubFile } from '@/utils';

export default {
  name: 'Menu',
  data() {
    return {
      isDeleteSpinnerOpened: false,
      isNewPageModalOpened: false,
      newPageName: '',
      isConfigModalOpened: false,
    };
  },

  methods: {
    deletePage(page) {
      this.isDeleteSpinnerOpened= true;
      var _this = this;
      function deleteConfig() {
        for(var i = 0; i < _this.config.pages.length; i++) {
          if(_this.config.pages[i].destination == page.destination) {
            _this.config.pages.splice(i, 1);
            break;
          }
        }
        _this.saveConfig();
      }
      this.$store.repository.deleteFile('gh-pages', page.fragment).then(() => {
        this.$store.repository.deleteFile('gh-pages', page.destination).then(() => {
          deleteConfig()
        }).catch(() => {
          this.$store.repository.deleteFile('gh-pages', page.destination).then(() => {
            deleteConfig();
          });
        });
      }).catch(() => {
        this.$store.repository.deleteFile('gh-pages', page.destination).then(() => {
          deleteConfig();
        }).catch(() => {
          deleteConfig();
        });
      });
    },
    openNewPageModal() {
      this.isNewPageModalOpened = true;
      this.newPageName = '';
    },
    closeNewPageModal() {
      this.isNewPageModalOpened = false;
    },
    createNewPage() {
      console.log(this.newPageNameSnake);
      this.config.pages.push({
        "title": this.newPageName,
        "template": "templates/compact.html",
        "fragment": "fragments/main.html",
        "destination": this.newPageNameSnake + ".html",
        "type": "html"
      });
      this.isNewPageModalOpened = false;
      this.newPageName = '';

      this.saveConfig(() => {});
    },

    loadPage(page, type) {
      this.$store.dispatch('changeEditedPage', page);
    },

    openConfig: function() {
      this.isConfigModalOpened = true;
    },

    saveConfig: function(cb) {
      this.$store.repository.writeFile(
        'gh-pages', // e.g. 'gh-pages'
        'vue-git-cms.json', // e.g. 'blog/index.md'
        JSON.stringify(this.config, null, 2), // e.g. 'Hello world, this is my new content'
        'compile config', // e.g. 'Created new index'
        (err) => {
          if(!err) {
            this.isConfigModalOpened = false;
            this.isDeleteSpinnerOpened = false;
            if(cb) cb();
          }
        }
      );
    },

    closeConfig: function() {
      this.isConfigModalOpened = false;
    },

    loadConfig() {
      var _this = this;
      return getGithubFile(this.$store, 'vue-git-cms.json', (responseText) => {
        var config = JSON.parse(atob(responseText.content));
        _this.$store.dispatch('setConfig', config);
      });
    },
  },

  watch: {
    authenticated() {
      this.loadConfig();
    }
  },

  computed: {
    newPageNameCamel(){
      function camelize(str) {
        return str.replace(/(?:^\w|[A-Z]|\b\w)/g, function(letter, index) {
          return index == 0 ? letter.toLowerCase() : letter.toUpperCase();
        }).replace(/\s+/g, '');
      }

      return camelize(this.newPageName);
    },
    newPageNameSnake(){
      var toSnakeCase = function(strSrc) {
        return strSrc.replace(/([a-z])([A-Z])/g, "$1-$2")
             .replace(/\s+/g, '-')
             .toLowerCase();

      };

      return toSnakeCase(this.newPageName);
    },
    ...mapState({
      authenticated: state => state.authenticated,
      config: state => state.config,
    })
  }

};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.menu {
  padding: 10px;
}

.menu-label {
  margin-left: 10px;
}

</style>



